        width: 100px;
        height: 100px;
        border-radius: 100%;  
    }
    .span1{
        width: 240px;
        position: absolute;
        top: 400px;
        text-align: center;
   }
   h1{
        color: rgb(108, 168, 142);
   }
   .button{
        position: fixed;
        bottom: 10px;
